.account {
	background-color: $grey100;
	padding: 24px 0;
}

.account__navigation {
	margin-bottom: 24px;
}

.account__main-data {
	margin-bottom: 4px;
}

.account__main-data,
.account__secondary-data {
	background-color: $white;
	border-radius: 12px;
	padding: 16px;
	box-shadow: 0px 1px 3px rgba(16, 24, 40, 0.1), 0px 1px 2px rgba(16, 24, 40, 0.06);
	position: relative;
	overflow: hidden;
	margin-bottom: 4px;

	&::before {
		content: '';
		height: 1px;
		width: 100vw;
		background-color: $grey200;
		position: absolute;
		bottom: 72px;
		left: 50%;
		transform: translateX(-50%);
	}
}

.account__secondary-data {
	padding-top: 24px;

	.account__area-fields-title {
		@include textXlSemibold;
		margin: 0;
		margin-bottom: 24px;
	}

	.account__area-fields-input {
		color: $grey500;

		&::placeholder {
			color: $grey500;
		}
	}
}

.account__navigation-list {
	@include reset-list;
	display: none;
}

.account__value {
	@include textMediumMedium;
	color: $grey900;
	background-color: $white;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 10px 18px 10px 14px;
	border: 1px solid $grey300;
	border-radius: 8px;
}

.account__area-edit-area {
	position: relative;
	width: max-content;
	margin-bottom: 24px;
}

.account__area-image {
	border-radius: 50%;
}

.account__area-edit {
	padding: 0;
	border: 0;
	background-color: $transparent;
	position: absolute;
	bottom: -5px;
	right: -5px;
}

.account__area-controls-list {
	@include reset-list;
	display: flex;
	gap: 12px;
	margin-bottom: 24px;
}

.account__area-controls-button {
	@include textSmallMedium;
	border-radius: 100px;
	border: 1px solid $grey300;
	background-color: $white;
	padding: 5px 11px;

	&--remove {
		background-color: $error600;
		border-color: $error600;
		color: $white;
	}
}

.account__area-fields {
	border: none;
	margin: 0;
	padding: 0;
	display: flex;
	flex-direction: column;

	&--password {
		margin-bottom: 32px;
	}
}

.account__area-fields-label {
	@include textSmallMedium;
	color: $grey700;
	display: block;
	margin-bottom: 6px;
}

.account__area-fields-input {
	@include textMediumRegular;
	color: $grey900;
	margin-bottom: 24px;
	border: 1px solid $grey300;
	border-radius: 8px;
	padding: 11px 13px;

	&::placeholder {
		color: $grey900;
	}
}

.account__area-gender-container {
	display: flex;
	gap: 24px;
	padding-bottom: 32px;

	.account__area-fields-input {
		&:checked {
			+ .account__area-gender {
				&::before {
					border-color: $primary500;
				}

				&::after {
					width: 6px;
					height: 6px;
					background-color: $primary500;
					left: 5px;
				}
			}
		}
	}
}

.account__area-gender {
	margin: 0;
	padding-left: 24px;
	display: flex;
	position: relative;

	&::before,
	&::after {
		content: '';
		border-radius: 50%;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
	}

	&::before {
		width: 16px;
		height: 16px;
		border: 1px solid $grey300;
		left: 0;
	}
}

.account__area-buttons {
	@include reset-list;
	display: flex;
	gap: 12px;
}

.account__area-item {
	width: calc(100% / 2 - 6px);
}

.account__area-button {
	@include textSmallMedium;
	background-color: $white;
	color: $grey700;
	width: 100%;
	padding: 8px;
	border: 1px solid $grey300;
	border-radius: 100px;

	&--save {
		background-color: $primary500;
		color: $white;
		border-color: $primary500;
	}
}